import React, { FC } from 'react'
import clsx from 'clsx'
import format from 'date-fns/format'
import ru from 'date-fns/locale/ru'
import { Typography } from '../../../other/Typography'
import { CalendarHeaderProps } from '../types'
import ArrowLeftNew from '../../../icons/ArrowLeftNew/ArrowLeftNew'
import ArrowRightNew from '../../../icons/ArrowRightNew/ArrowRightNew'
import '../Calendar.scss'

const CalendarHeader: FC<CalendarHeaderProps> = ({
  date,
  decreaseMonth,
  increaseMonth,
  locale = ru,
  prevMonthButtonDisabled,
  nextMonthButtonDisabled,
}) => {
  return (
    <div className='CalendarNext__Header'>
      <button
        className={clsx(
          'reset-button-styles',
          'CalendarNext__HeaderButton',
          'CalendarNext__HeaderButton_Prev',
        )}
        disabled={prevMonthButtonDisabled}
        onClick={decreaseMonth}
      >
        <ArrowLeftNew />
      </button>

      <Typography
        className='CalendarNext__HeaderDate'
        text={format(date, 'LLLL yyyy', { locale })}
        type='boldBody'
      />
      <button
        className={clsx(
          'reset-button-styles',
          'CalendarNext__HeaderButton',
          'CalendarNext__HeaderButton_Next',
        )}
        disabled={nextMonthButtonDisabled}
        onClick={increaseMonth}
      >
        <ArrowRightNew />
      </button>
    </div>
  )
}

export default CalendarHeader
